import Vue from 'vue'
import Router from 'vue-router'
import index from '@/view/index'
import antibacterial from '@/view/antibacterial'
import cxsdkjfw from '@/view/cxsdkjfw'
import disinfection from '@/view/disinfection'
import join from '@/view/join'
import photocatalysis from '@/view/photocatalysis'
import reservation from '@/view/reservation'
import safety from '@/view/safety'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
	  meta: {
		title: ''
	  }
    },
	{
		path: '/antibacterial',
		name: 'antibacterial',
		component: antibacterial,
		meta: {
			title: ''
		}
	},
	{
		path: '/cxsdkjfw',
		name: 'cxsdkjfw',
		component: cxsdkjfw,
		meta: {
			title: ''
		}
	},
	{
		path: '/disinfection',
		name: 'disinfection',
		component: disinfection,
		meta: {
			title: ''
		}
	},
	{
		path: '/join',
		name: 'join',
		component: join,
		meta: {
			title: ''
		}
	},
	{
		path: '/photocatalysis',
		name: 'photocatalysis',
		component: photocatalysis,
		meta: {
			title: ''
		}
	},
	{
		path: '/reservation',
		name: 'reservation',
		component: reservation,
		meta: {
			title: ''
		}
	},
	{
		path: '/safety',
		name: 'safety',
		component: safety,
		meta: {
			title: ''
		}
	}
  ],
})



